import React from 'react';
// import { Link } from "react-router-dom";
// import { Image } from "react-bootstrap";
// import three_troops from "../images/3troops.gif";
// import banner from "../images/banner.png";
import "./Home.css";
import client from './CLASSES/client';
import Pure from './Pure';

class Home extends React.PureComponent {
    render() {
        return <>
            <div className="home">
                <h3>مرحباً بكم</h3>
                {/* <h3>مرحباً بكم في حرب التتار</h3> */}
                <div className="box-full">
                    <h1 className="box-title">استراتيجية للخبراء</h1>
                    {/* <h1 className="box-title">لعبة استراتيجية للخبراء</h1> */}
                    <div>رائدة الألعاب الجماعية على الإنترنت بالفعل مع آلاف اللاعبين الحقيقيين.</div>
                </div>
                <div className="box-banner">
                    <div className="box-small">
                        <div><b>عن اللعبة</b></div>
                        <div>• ستبدأ كرئيس قرية صغيرة.</div>
                        <div>• ستطور المباني والحقول والجيش.</div>
                        <div>• ستحارب مع أو ضد لاعبين حقيقيين.</div>
                    </div>
                    <div className="box-small">
                        <Stats wait={true} />
                    </div>
                </div>
            </div>
        </>
    }
}

class Stats extends Pure {
    state = { time: 0 };
    players = 0;
    online = 0;
    servers = 0;
    onReady() {
        client.homePage(response => {
            this.players = response.players;
            this.online = response.online;
            this.servers = response.servers;
            this.updateState({ time: Date.now() });
        });
    }
    render() {
        return <>
            <div><b>إحصائيات</b></div>
            <div>• عدد اللاعبين <b>{this.players}</b></div>
            <div>• عدد المتواجدين حالياً <b>{this.online}</b></div>
            <div>• عدد السيرفرات <b>{this.servers}</b></div>
        </>;
    }
}

export default Home;